import React from 'react';
import { Row} from 'react-bootstrap';
import '../../src/app.css';
import './feedcard.css';
import { animated, useSprings } from 'react-spring';

const FeedCard = ({feed}) => {
    const {id} = feed;
    return(
        <animated.div className="main-content-feedcard">
            Feed no. {id} is displayed.
        </animated.div>
    )
}


export default FeedCard;